.holder {
  background-color: lightgray;
  display: block;
  max-width: 200px;
  height: 200px;
}

.btn {
  padding: .5rem 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
  border-radius: 0;
}

body {
  font-size: .875rem;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

/*
 * Sidebar
 */
.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  /* Behind the navbar */
  padding: 48px 0 0;
  /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1);
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto;
  /* Scrollable contents if viewport is shorter than content. */
}

@supports (position: -webkit-sticky) or (position: sticky) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active {
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

/*
 * Content
 */
[role="main"] {
  padding-top: 48px;
  /* Space for fixed navbar */
}

/*
 * Navbar
 */
.navbar-brand {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.25);
}

.navbar .form-control {
  padding: .75rem 1rem;
  border-width: 0;
  border-radius: 0;
}

.form-control-dark {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
}

.form-control-dark:focus {
  border-color: transparent;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.25);
}

nav.navbar a {
  text-decoration: none;
}

.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 70px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  z-index: 5;
  padding: 0 20px 0 66px;
  /* Dropdown Content (Hidden by Default) */
}

@media (min-width: 1025px) {
  .header {
    height: 140px;
    font-size: 14px;
    padding: 0 30px 0 114px;
  }
}

.header--scrolled {
  background: #FFF;
}

.header__logo {
  text-align: center;
}

.header__logo-img {
  width: 40px;
  height: 40px;
}

@media (min-width: 1025px) {
  .header__logo-img {
    width: 100px;
    height: 100px;
  }
}

.header__slogan {
  display: block;
  text-transform: uppercase;
  color: #000;
  transition: color 0.4s;
  text-decoration: none;
  margin-top: 5px;
  white-space: nowrap;
}

.header__slogan:hover {
  color: #115EAC;
}

.header__menu {
  display: none;
}

@media (min-width: 768px) {
  .header__menu {
    display: flex;
    flex-grow: 1;
  }
}

.header__button-group {
  position: relative;
  flex-grow: 1;
  height: 20px;
}

@media (min-width: 768px) {
  .header__button-group {
    max-width: 80px;
    flex: 1 0 80px;
    height: 40px;
  }
}

.header__button-group--unlock .header__dropdown-content:before {
  border-right-color: #0074bc;
}

.header__button-group--unlock .header__dropdown-content a {
  background-color: #0074bc;
}

@media (min-width: 768px) {
  .header__button-group--unlock {
    flex: 1 0 200px;
    max-width: 200px;
    height: 40px;
  }
}

@media (min-width: 1025px) {
  .header__button-group--unlock {
    flex: 1 0 250px;
    max-width: 250px;
  }
}

.header__login-button-box {
  width: 22px;
  height: 22px;
  position: absolute;
  right: 22px;
}

.header__login-button-box:focus .header__dropdown-content, .header__login-button-box:active .header__dropdown-content, .header__login-button-box:hover .header__dropdown-content {
  display: block;
}

.header__login-button-box--hover-off:focus .header__dropdown-content, .header__login-button-box--hover-off:active .header__dropdown-content, .header__login-button-box--hover-off:hover .header__dropdown-content {
  display: none;
}

.header__login-button-box .user-name {
  color: #0074bc;
  text-transform: uppercase;
  font-size: 10px;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: absolute;
  top: 6px;
  right: 24px;
  max-width: 132px;
}

@media (max-width: 360px) {
  .header__login-button-box .user-name {
    max-width: 94px;
  }
}

@media (min-width: 1025px) {
  .header__login-button-box .user-name {
    font-size: 12px;
    max-width: 156px;
    top: 14px;
    right: 40px;
  }
}

@media (min-width: 768px) {
  .header__login-button-box {
    top: 10px;
  }
}

@media (min-width: 1025px) {
  .header__login-button-box {
    height: 40px;
    width: 40px;
    top: 0;
    right: 42px;
  }
}

.header__login-button {
  display: flex;
  margin-left: auto;
  position: static;
  right: 30px;
}

@media (min-width: 1025px) {
  .header__login-button {
    position: static;
    right: 60px;
  }
}

.header__dropdown-content {
  display: none;
  position: absolute;
  background-color: #000;
  min-width: 160px;
  z-index: 1;
  margin-right: 10px;
  top: 50px;
  right: 0;
}

.header__dropdown-content:before {
  margin-right: 10px;
  border-top: 30px solid transparent;
  border-right: 20px solid #000;
  position: absolute;
  cursor: pointer;
  width: 160px;
  content: '';
  top: -30px;
  right: -10px;
}

.header__dropdown-content a {
  color: #FFF;
  padding: 12px 16px;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  border-bottom: 1px solid #FFF;
  background-color: #000;
}

.header__dropdown-content a:last-child {
  border: none;
}

.header__dropdown-content a:hover {
  color: #d8d8d8;
}

@media (min-width: 1025px) {
  .header__dropdown-content {
    right: 8px;
    top: 64px;
  }
}

.header__search-button {
  right: 0;
}

#js_form {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

#js_form .field {
  margin-bottom: 10px;
}

#js_instagram-add-account,
#js_login-form {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}

#js_instagram-add-account .field,
#js_login-form .field {
  margin-bottom: 10px;
}

.sidebar-sticky .nav-link {
  text-decoration: none;
}



